body {
  margin: 0%;
  padding: 0%;
}
.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}


.header {
  flex: 30%;
  background-color: tomato;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .inputContainer {
  display: flex;
  flex-direction: column;
}
.header input {
  width: 200px;
  height: 40px;
  border: none;
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
  padding-left: 10px;
  font-size: 17px;
  border: 1px solid grey;
  font-weight: 600;
  color: black;
}
::placeholder {
  color: tomato;
}

.header button {
  width: 70px;
  height: 87px;
  border: none;
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
  padding-left: 10px;
  cursor: pointer;
  background-color: lightseagreen;
  font-weight: 600;
  color: white;
}
.todoList {
  flex: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
  background-color: antiquewhite;
}
.task {
  width: 500px;
  height: 50px;
  display: flex;
  color: white;
  margin: 15px;
}

.task .content {
  flex: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task .content span {
  display: grid;
  place-items: center;
  border: 1px solid white;
  width: 100%;
  height: 100%;
  font-size: 18px;
  border-right: none;
  background-color: tomato;
}

.task button {
  flex: 20%;
  height: 100%;
  border: none;
  background-color: lightseagreen;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  color: white;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  .task {
    width: 360px;
    max-width: 100%;

  }
  .task .content .nmbr {
    padding-left: 5px;
    width: auto;
    padding-right: 6.5px;
  }
  .task .content .writting {
    width: 1fr;
    padding-left: 2px;
    padding-right: 2px;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;    
  }
  .task button {
    flex: 5%;

  }
}